(function() {
  'use strict';

  angular
    .module('greenboothDesign', ['ngResource', 'ui.router', 'ngMaterial', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('greenboothDesign')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($log, $window) {
      var vm = this;
      vm.scrollTop = scrollTop;
    
      function scrollTop() {
        $window.scrollTo(0,0);
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('greenboothDesign')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($log, $window) {
    var vm = this;
    vm.hourlyRate = 45;

    activate();

    function activate() {
      $log.info('Main page active.')
      angular.element($window).on('scroll', function () {
          setHeaderOpacity();
      });
    }

    function setHeaderOpacity() {
        var header = angular.element('md-toolbar.my-theme');
        var e = angular.element('#banner');

        var bannerHeight = parseInt(e.css('height').replace("px", ""));
        var offset = $window.pageYOffset;
        var headerHeight = parseInt(header.css('height').replace("px", ""));
        var opacity = offset / (bannerHeight - headerHeight); 

        if(opacity < 0) opacity = 0;
        if(opacity > 1) opacity = 1;
        
        header.css('background-color', 'rgba(66,66,66,' + opacity + ')');
        header.css('box-shadow', 'box-shadow: 1px 1px 10px rgba(0,0,0,' + opacity/10 + ')');
    }

    vm.services = [
      {
        name: 'Basic Space',
        desc: 'A clean and customized squarespace site with a personalized content strategy.'
      },
      {
        name: 'Photography',
        desc: 'A 1-hour photoshoot for modern, clean, straightforward images. Completely edited and customized.'
      },
      {
        name: 'Asset Makeover',
        desc: 'Updated or new custom art assets. Good for logos, watermarks, filters, etc.'
      },
      {
        name: 'Custom Site',
        desc: 'The works. If you can dream it, we can build it.'
      }
    ];

    vm.projects = [
      {
        name: 'Roe with the Greens',
        logo: './assets/images/lularoe-square.png',
        link: 'http://www.roewiththegreens.com/'
      },
      {
        name: 'Saving Susan Ministry',
        logo: './assets/images/ssm-white.png',
        link: 'https://savingsusanministry.org/'
      },
      {
        name: 'Live and Love Ministry',
        logo: 'https://www.liveandloveministry.org/favicon.ico',
        link: 'https://liveandloveministry.org/'
      },
      {
        name: 'LMC Interior Designs',
        logo: './assets/images/lmc.png',
        link: 'http://www.lmcinteriordesigns.com/'
      }  
    ];
  }
})();

(function() {
  'use strict';

  angular
    .module('greenboothDesign')
    .run(runBlock);

  /** @ngInject */
  function runBlock() {}

})();

(function() {
  'use strict';

  angular
    .module('greenboothDesign')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('greenboothDesign')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('greenboothDesign')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

